import { filter } from 'lodash';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

// @mui
import {
  Paper,
  Grid,
  Button,
  Popover,
  MenuItem,
  Typography,
  TablePagination,
  Box,
  useTheme,
  ListItemIcon,
} from '@mui/material';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';

// import { fDate } from 'utils/formatTime'
import CustomerListToolbar from './sections/CustomerListToolbar';
import CustomerCard from './sections/CustmerCard';


// mock
import { customers } from '__mocks__/customers';
import { IconPencil, IconPlus, IconTrash } from '@tabler/icons';

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_user) => _user.name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis.map((el) => el[0]);
}

export default function CustomerCardList() {
  const theme = useTheme();
  const navigate = useNavigate();

  const [open, setOpen] = useState(null);

  const [page, setPage] = useState(0);

  // const [order, setOrder] = useState('asc');


  // const [orderBy, setOrderBy] = useState('name');

  const [filterName, setFilterName] = useState('');

  const [rowsPerPage, setRowsPerPage] = useState(9);

  const items = customers;


  const handleCloseMenu = () => {
    setOpen(null);
  };




  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPage(0);
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleFilterByName = (event) => {
    setPage(0);
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - items.length) : 0;

  const filteredItems = applySortFilter(items, getComparator('asc', 'Name'), filterName);

  const isNotFound = !filteredItems.length && !!filterName;

  return (
    <PageContainer>
      <PageHeader title="Customers" action={
        <Button variant="contained" startIcon={<IconPlus />}
          onClick={() => navigate('/pages/customers/add', { replace: true })}
        >
          New Customer
        </Button>
      } />

      <Box sx={{ flexGrow: 1 }}>
        {/* <MainCard sx={{ flexGrow: 1, pt: 0 }}> */}
        <CustomerListToolbar numSelected={0} filterName={filterName} onFilterName={handleFilterByName} />

        {/* <Scrollbar> */}

        <Grid container spacing={3}>
          {filteredItems.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
            // const { id, name, avatarUrl, email, address, phone, createdAt } = row;
            // const selectedUser = selected.indexOf(name) !== -1;
            return (
              <Grid item container lg={4} md={4} sm={12} xs={12} key={row.id}>
                <CustomerCard user={row} />
              </Grid>
            );
          })}
        </Grid>

        {emptyRows > 0 && (
          <Typography> </Typography>
        )}
        {isNotFound && (
          <Paper
            sx={{
              textAlign: 'center',
            }}
          >
            <Typography variant="h6" paragraph>
              Not found
            </Typography>

            <Typography variant="body2">
              No results found for &nbsp;
              <strong>&quot;{filterName}&quot;</strong>.
              <br /> Try checking for typos or using complete words.
            </Typography>
          </Paper>
        )}

        {/* </Scrollbar> */}

        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={items.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
        {/* </MainCard> */}
        <Popover
          open={Boolean(open)}
          anchorEl={open}
          onClose={handleCloseMenu}
          anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
          transformOrigin={{ vertical: 'top', horizontal: 'right' }}
          PaperProps={{
            sx: {
              p: 1,
              width: 140,
              '& .MuiMenuItem-root': {
                px: 1,
                typography: 'body2',
                borderRadius: `${theme.shape.borderRadius}px`,
              },
            },
          }}
        >
          <MenuItem>
            <ListItemIcon fontSize="small" >
              <IconPencil />
            </ListItemIcon>
            Edit
          </MenuItem>

          <MenuItem sx={{ color: 'error.main' }}>
            <ListItemIcon fontSize="small" >
              <IconTrash />
            </ListItemIcon>
            Delete
          </MenuItem>
        </Popover>
      </Box>
    </PageContainer>
  );
}
